@use '../util/' as *;

h1,h2,h3{
  margin-top: 0;
}

a {display: block;}

p a {display: inline;}

li {list-style-type: none;}

h1,h2,h3,h4,h5,h6,
p,span,a,strong,blockquote,
i,b,u,em {
  font-size: 1em;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: none;
  color: inherit;
}

h1{
  font-size: rem(24);
  @include breakpoint(large){
    font-size: rem(28);
  }
}

h2{
  font-size: rem(20);
  @include breakpoint(large){
    font-size: rem(24);
  }
}

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: "";
  content: none;
}

form,input,textarea,
select,button,label {
  font-family: inherit;
  font-size: inherit;
  // hyphens: auto; 
  background-color: transparent;
  display: block;
  color: inherit;
}