:root {
  --lime-green:hsl(163, 72%, 41%);
  --bright-red:hsl(356, 69%, 56%);
  --facebook:hsl(208, 92%, 53%);
  --twitter:hsl(203, 89%, 53%);
  --instagram-start:hsl(37, 97%, 70%);
  --instagram-end:hsl(329, 70%, 58%);
  --youtube:hsl(348, 97%, 39%);
  --toggle-start:hsl(210, 78%, 56%);
  --toggle-end:hsl(146, 68%, 55%);
  --toggle:hsl(230, 22%, 74%);
  --bg:hsl(0, 0%, 100%);
  --top-bg:hsl(225, 100%, 98%);
  --bg-card:hsl(227, 47%, 96%);
  --card-hover:hsla(0, 0%, 100%, 0.307);
  --color-text-1:hsl(228, 12%, 44%);
  --color-text-2:hsl(230, 17%, 14%);
}

body.dark {
  --bg:hsl(230, 17%, 14%);
  --top-bg:hsl(232, 19%, 15%);
  --bg-card:hsl(228, 28%, 20%);
  --card-hover:hsl(228, 25%, 27%);
  --color-text-1:hsl(228, 34%, 66%);
  --color-text-2:hsl(0, 0%, 100%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none;
    transition: none;
  }
}
*, *::before, *::after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

img,
picture,
video,
iframe,
figure {
  max-width: 100%;
  width: 100%;
  display: block;
  object-fit: cover;
  object-position: center center;
}

html {
  scroll-behavior: smooth;
}

table, tr, td {
  border-collapse: collapse;
  border-spacing: 0;
}

svg {
  width: 100%;
  display: block;
  fill: currentColor;
}

body {
  max-width: 100%;
  font-size: 100%;
  font-family: var(--font-inter);
  color: var(--color-text-2);
  background: var(--bg);
  line-height: 1.4em;
  font-smooth: always;
  --webkit-font-smoothing: antialiased;
  --moz-osx-font-smoothing: grayscale;
  transition: 0.5s ease-in-out;
}

h1, h2, h3 {
  margin-top: 0;
}

a {
  display: block;
}

p a {
  display: inline;
}

li {
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6,
p, span, a, strong, blockquote,
i, b, u, em {
  font-size: 1em;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: none;
  color: inherit;
}

h1 {
  font-size: 1.5rem;
}
@media (min-width: 71.875rem) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.25rem;
}
@media (min-width: 71.875rem) {
  h2 {
    font-size: 1.5rem;
  }
}

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: "";
  content: none;
}

form, input, textarea,
select, button, label {
  font-family: inherit;
  font-size: inherit;
  background-color: transparent;
  display: block;
  color: inherit;
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.8125rem;
}
@media (min-width: 71.875rem) {
  .container {
    padding: 0;
  }
}
.container__subtitle {
  margin-bottom: 2rem;
  font-weight: 700;
}

.header {
  margin-top: 2.25rem;
}
@media (min-width: 40rem) {
  .header {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    gap: 0;
  }
}
.header__title {
  width: 100%;
  font-weight: 700;
}
.header__title--h1 {
  margin-bottom: 0.3125rem;
}
.header__title--span {
  font-weight: 700;
  font-size: 0.875rem;
  color: var(--color-text-1);
}
.header__toggle {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  align-items: center;
  width: 13rem;
}
@media (max-width: 39.9375rem) {
  .header__toggle {
    width: 100%;
    justify-content: space-between;
    border-top: 1px solid var(--color-text-1);
    padding-top: 0.8rem;
    margin-top: 0.8rem;
  }
}

.cards {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-items: center;
  gap: 2rem;
}

.card {
  min-width: 15.625rem;
  border-radius: 0.3rem;
  background: var(--bg-card);
  color: var(--color-text-1);
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  padding: 1.25rem 0;
  transition: 0.4s ease-in-out;
  cursor: pointer;
}
@media (max-width: 39.9375rem) {
  .card {
    min-width: 100%;
  }
}
.card:hover {
  background: var(--card-hover);
  backdrop-filter: blur(5rem);
  box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.3);
}
.card--facebook {
  border-top: 0.25rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.25rem solid var(--twitter);
}
.card--instagram {
  position: relative;
  overflow: hidden;
}
.card--instagram::before {
  content: "";
  height: 0.25rem;
  width: 100%;
  top: 0;
  position: absolute;
  background: linear-gradient(to right, var(--instagram-start) 0%, var(--instagram-end) 60%);
}
.card--youtube {
  border-top: 0.25rem solid var(--youtube);
}
.card__platform {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
}
.card__icon--svg {
  width: 1.6rem;
}
.card__username {
  font-size: 0.875rem;
  font-weight: 700;
}
.card__count {
  text-align: center;
  color: var(--color-text-2);
  font-weight: 700;
}
.card__count--big {
  font-size: 2.8125rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  font-size: 1rem;
  letter-spacing: 0.2rem;
  color: var(--color-text-1);
  font-weight: 400;
  margin-top: 0.8rem;
}
.card__change {
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  gap: 0.3rem;
  font-size: 0.9rem;
  font-weight: 700;
}
.card__change img {
  max-width: 0.65rem;
}
.card__change--up {
  color: var(--lime-green);
}
.card__change--down {
  color: var(--bright-red);
}
.card__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}

.card-grid {
  display: grid;
  grid-template-areas: "sub icon" "num per";
  padding: 2rem 1.5rem;
}
.card-grid .card__subtitle {
  grid-area: sub;
}
.card-grid .card__icon {
  grid-area: icon;
  margin-left: auto;
}
.card-grid .card__count {
  margin-right: auto;
  grid-area: num;
}
.card-grid .card__change {
  grid-area: per;
  margin-left: auto;
  width: auto;
}

.toggle label {
  transition: 0.5s ease-in-out;
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--color-text-1);
  cursor: pointer;
}
@media (max-width: 39.9375rem) {
  .toggle label {
    font-size: 1rem;
  }
}
.toggle__wrapper {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  gap: 0;
  height: 1.875rem;
  width: 3.875rem;
  min-width: 3.875rem;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  transition: 0.5s ease-in-out;
}
.toggle__wrapper #light:checked ~ .toggle__button {
  left: calc(100% - 1.625rem);
  right: 0.1875rem;
}
.toggle__wrapper #light:checked ~ .toggle__background {
  background: var(--toggle);
}
.toggle__wrapper #dark:checked ~ .toggle__button {
  left: 0.1875rem;
  right: calc(100% - 1.625rem);
}
.toggle__background {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border-radius: 0.9375rem;
  z-index: -1;
  background: linear-gradient(225deg, var(--toggle) -10%, var(--toggle-end) 0%, var(--toggle-start) 100%);
  pointer-events: none;
}
.toggle input[type=radio] {
  margin: 0;
  opacity: 0;
  width: 1.875rem;
  height: 1.875rem;
  cursor: pointer;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  width: 1.4375rem;
  height: 1.4375rem;
  border-radius: 50%;
  background-color: var(--bg);
  transition: 0.35s ease-in-out;
}

.attribution {
  position: fixed;
  padding: 0 0 0 1.5rem;
  background: linear-gradient(var(--toggle-start) 0%, var(--toggle-end) 100%);
  border-radius: 50px;
  bottom: 10px;
  right: 10px;
  width: 4rem;
  height: 4rem;
  overflow: hidden;
  transition: 0.5s ease-in-out;
  text-align: left;
  line-height: 1.2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9rem;
}
.attribution__text {
  transition: 0.3s 0.3s;
  opacity: 0;
  min-width: 19rem;
  margin: 0.2rem 0 0;
  color: var(--color-text-2);
  font-weight: 600;
}
.attribution__text--a {
  color: var(--bg);
}
.attribution__img {
  position: absolute;
  bottom: 0;
  right: 0;
  max-width: 4rem;
  border: 4px solid var(--color-text-2);
  border-radius: 50%;
  cursor: pointer;
  transition: 0.5s ease-in-out;
}
.attribution:hover {
  width: 19.5rem;
  border-radius: 50px;
}
.attribution:hover .attribution__text {
  opacity: 1;
}